.movie {
  transition: all 0.2s ease-in-out;
  &:hover {
    transform: scale(1.05);
  }
}
.movie__summary {
  max-height: 200px;
}
.movie img {
  position: relative;
  top: -50px;
  max-width: 120px;
  margin-right: 30px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
.movie__header {
  display: flex;
  justify-content: center;
  align-items: center;
}

.movie .movie__title,
.movie .movie__year {
  margin: 0;
  font-weight: 300;
}
.movie .movie__year {
  margin-left: 10px;
}
.movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}
.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0px;
}
.movie__genres li,
.movie .movie__year {
  margin-right: 10px;
  font-size: 14px;
}

.movie__summary {
  overflow-y: scroll;
}
